header {
  position: sticky;
  top: 0;
  min-height: 80px;
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(122, 122, 243);
  text-decoration: none;
  color: black;
}

.Contact,
.Cart,
.Home,
.Title {
  text-decoration: none;
  list-style-type: none;
  font-size: bold;
}

.Cart {
  text-decoration: none;
}

ul {
  list-style-type: none;
}
